<template>
  <div style="width: 100%; height: 100%; background-color: white">
    <div style="font-size: 2vw; line-height: 12vh; font-weight: 900">
      
    </div>
    <div>
      <el-alert style="width: 96%; margin-left: 2vw; height: 10vh" type="warning" :closable="false">
        <div style="font-size: 1vw; font-weight: 900; text-align: left">
          
        </div>
        <div style="font-size: 1vw; font-weight: 500">
          
        </div>
      </el-alert>
      <div style="width: 96%; margin-left: 2vw">
        <div style="height: 1vh"></div>
        <el-divider content-position="left" style="font-size: 1.5vw">
          <div class="my_font"></div>
        </el-divider>
        <div style="height: 1vh"></div>
        <div style="display: flex; height: 3vh">
          <div style="font-size: 0.8vw; line-height: 4vh; font-weight: 900">
            :
          </div>
          <div style="width: 2%"></div>
          <el-input style="width: 8%; height: 3vh" placeholder="" v-model="inputRecord" :disabled="true">
          </el-input>
          <span style="margin-left: 10px; font-size: 1.1vw; line-height: 5vh">MB</span>
          <div style="width: 2%"></div>

          <el-button type="danger" class="my_button" @click="deleteRecord"></el-button>
        </div>
        <div style="height: 1vh"></div>
        <div style="height: 1vh"></div>
        <el-divider content-position="left" style="font-size: 1.5vw">
          <div class="my_font"></div>
        </el-divider>
        <div style="height: 1vh"></div>
        <div style="height: 1vh"></div>
        <div style="display: flex; height: 3vh">
          <div style="font-size: 0.8vw; line-height: 4vh; font-weight: 900">
            :
          </div>
          <div style="width: 2%"></div>
          <el-input style="width: 8%; height: 3vh" placeholder="" v-model="inputVideo" :disabled="true">
          </el-input>
          <span style="margin-left: 10px; font-size: 1.1vw; line-height: 5vh">MB</span>
          <div style="width: 2%"></div>

          <el-button type="danger" class="my_button" @click="deleteVideo"></el-button>
        </div>
        <div style="height: 1vh"></div>
        <div style="height: 1vh"></div>
        <el-divider content-position="left" style="font-size: 1.5vw">
          <div class="my_font"></div>
        </el-divider>
        <div style="height: 1vh"></div>
        <div style="display: flex; height: 3vh">
          <div style="font-size: 0.8vw; line-height: 4vh; font-weight: 900">
            :
          </div>
          <div style="width: 2%"></div>
          <el-input style="width: 8%; height: 3vh" placeholder="" v-model="inputPhoto" :disabled="true">
          </el-input>
          <span style="margin-left: 10px; font-size: 1.1vw; line-height: 5vh">MB</span>
          <div style="width: 2%"></div>
          <el-button type="danger" class="my_button" @click="deletePhoto"></el-button>
        </div>
        <div style="height: 1vh"></div>

        <!--  -->
<div style="height: 1vh"></div>
<el-divider content-position="left" style="font-size: 1.5vw">
  <div class="my_font"></div>
</el-divider>
<div style="height: 1vh"></div>

<div style="display: flex; align-items: center; height: 3vh; width: 100%">
  <div style="font-size: 1.1vw; margin-right: 10px;"></div>
  <el-button type="primary" class="my_button" @click="backupAllPhotos(2)" style="min-width: 100px;">
    
  </el-button>

  <div style="font-size: 1.1vw; margin-left: 20px; margin-right: 10px;"></div>
  <el-button type="primary" class="my_button" @click="backupOriginalPhotos(1)" style="min-width: 100px;">
    
  </el-button>
</div>

<div style="height: 1vh"></div>

<el-divider content-position="left" style="font-size: 1.5vw">
  <div class="my_font"></div>
</el-divider>
<div style="height: 1vh"></div>

<div style="display: flex; align-items: center; height: 3vh; width: 100%">
  <div style="font-size: 1.1vw; margin-right: 10px;"></div>
  <el-button type="primary" class="my_button" @click="backupAllVideos(2)" style="min-width: 100px;">
    
  </el-button>

  <div style="font-size: 1.1vw; margin-left: 20px; margin-right: 10px;"></div>
  <el-button type="primary" class="my_button" @click="backupOriginalVideos(1)" style="min-width: 100px;">
    
  </el-button>
</div>


        <div style="height: 1vh"></div>
      </div>
    </div>
  </div>
  <!--  -->
</template>
<script>
export default {
  data() {
    return {
      inputRecord: "",
      inputVideo: "",
      inputPhoto: "",
    };
  },
  mounted() {
    this.recordStore();
    this.videoStore();
    this.photoStore();
  },
  methods: {
    async backupAllPhotos(style) {
      try {
        const response = await fetch(
          this.$apiBaseUrl + `/download_all_photos?style=${style}`
        );
        
        if (!response.ok) {
          throw new Error("Network response was not ok");
        }

        //  response 
        const blob = await response.blob();
        const link = document.createElement("a");
        link.href = URL.createObjectURL(blob);
        link.download = "all_photos.zip"; // 
        link.click();
      } catch (error) {
        console.error(":", error);
      }
    },
    async backupOriginalPhotos(style) {
      try {
        const response = await fetch(
          this.$apiBaseUrl + `/download_all_photos?style=${style}`
        );
        
        if (!response.ok) {
          throw new Error("Network response was not ok");
        }

        //  response 
        const blob = await response.blob();
        const link = document.createElement("a");
        link.href = URL.createObjectURL(blob);
        link.download = "original_photos.zip"; // 
        link.click();
      } catch (error) {
        console.error(":", error);
      }
    },
    async backupAllVideos(style) {
      try {
        const response = await fetch(
          this.$apiBaseUrl + `/download_all_video?style=${style}`
        );
        
        if (!response.ok) {
          throw new Error("Network response was not ok");
        }

        //  response 
        const blob = await response.blob();
        const link = document.createElement("a");
        link.href = URL.createObjectURL(blob);
        link.download = "all_videos.zip"; // 
        link.click();
      } catch (error) {
        console.error(":", error);
      }
    },
    async backupOriginalVideos(style) {
      try {
        const response = await fetch(
          this.$apiBaseUrl + `/download_all_video?style=${style}`
        );
        
        if (!response.ok) {
          throw new Error("Network response was not ok");
        }

        //  response 
        const blob = await response.blob();
        const link = document.createElement("a");
        link.href = URL.createObjectURL(blob);
        link.download = "original_videos.zip"; // 
        link.click();
      } catch (error) {
        console.error(":", error);
      }
    },
    async recordStore() {
      // 
      let data = {
        type: "record",
      };

      try {
        //  await 
        const response = await this.$axios.post(
          this.$apiBaseUrl + "/get_sizes",
          data
        );
        this.inputRecord = response.data.size;
      } catch (error) {
        // 
        console.error(":", error);
      }
    },

    async videoStore() {
      // 
      let data = {
        type: "record",
      };

      try {
        //  await  POST 
        const response = await this.$axios.post(
          this.$apiBaseUrl + "/get_sizes",
          data
        );
        this.inputVideo = response.data.size;
      } catch (error) {
        // 
        console.error(":", error);
      }
    },

    async photoStore() {
      // 
      let data = {
        type: "photo",
      };

      try {
        //  await  POST 
        const response = await this.$axios.post(
          this.$apiBaseUrl + "/get_sizes",
          data
        );
        this.inputPhoto = response.data.size;
      } catch (error) {
        // 
        console.error(":", error);
      }
    },

    deleteRecord() {
      let data = {
        type: "video",
      };
      this.$axios
        .post(this.$apiBaseUrl + "/delete", data)
        .then((response) => {
          this.recordStore();
        })
        .catch((error) => {
          console.error(":", error);
        });
    },
    deleteVideo() {
      let data = {
        type: "video",
      };
      this.$axios
        .post(this.$apiBaseUrl + "/delete", data)
        .then((response) => {
          this.videoStore();
        })
        .catch((error) => {
          console.error(":", error);
        });
    },
    deletePhoto() {
      let data = {
        type: "photo",
      };
      this.$axios
        .post(this.$apiBaseUrl + "/delete", data)
        .then((response) => {
          this.photoStore();
        })
        .catch((error) => {
          console.error(":", error);
        });
    },
  },
};
</script>
<style>
.my_font {
  font-size: 1.1vw;
  font-weight: 600;
}

.my_button {
  height: 5vh;
  width: 6vw;
  font-size: 0.8vw;
  margin-left: 0.5vw;
  margin-right: 0.5vw;
  font-weight: 900;
}
</style>



