<script>
import myvideo from "../../video/myvideo.vue";
export default {
  components: {
    myvideo,
  },
  data() {
    return {
      showSearch: false,
      searchForm: {
        photoName: '',
        processType: "video",
        startTime: null,
        endTime: null,
        kindCount: null,
      },
      photo_name: "",
      process_type: "",
      upload_time: "",
      process_time: "",
      number: 0,
      kind: 0,
      isDragging: false, // 
      offsetX: 0,
      offsetY: 0,
      currentPage: 1,  // el-pagination 
      pageNum: 1,      // 
      pageSize: 3,     // 
      total: 0,        // 
      tableData: {
        photos: [],     // 
      },
      total: 0,
      size: false,
      detail: false,
      fastElimination: false,
      dark: false,
      CLAHE: false,
      Sharpening: false,
      isShowVideo: false,
      drawerVisible: false,
      activeIndex: "4", // 
      videoUrl: "",
      uploadUrl: "",
      showProgress: false,
      progressPercentage: 0,
      isShowLocalVideo: true,
      VideoUrl_: "",
      detailPhotoName: "",
      logDetail: false,
    };
  },
  computed: {

    paginatedData() {
      if (!this.tableData || !Array.isArray(this.tableData.photos)) return [];

      const photos = this.tableData.photos;  //  photos 
      const start = (this.currentPage - 1) * this.pageSize;
      const end = this.currentPage * this.pageSize;

      return photos.slice(start, end); //  photos 
    },

    drawer_class_ctrl() {
      return this.drawerVisible ? "drawer-open" : "drawer-close";
    },
    drawer_button_class_ctrl() {
      return [
        this.drawerVisible ? "drawer-button-open" : "drawer-button-close",
        this.drawerVisible ? "el-icon-caret-right" : "el-icon-caret-left",
      ];
    },
  },
  created() {
    //  cameraUrl
    this.uploadUrl = this.$apiBaseUrl + "/uploadVideo";
  },
  mounted() {
    this.total = this.tableData.length; // 
    this.$notify({
      title: '',
      message: ':',
      type: 'warning'
    });
  },
  methods: {
    onReset() {
      this.searchForm = {
        photoName: '',
        processType: "video",
        startTime: null,
        endTime: null,
        kindCount: null,
      };
      this.tableData.photos = [];
    },
    handlePageChange(newPage) {
      this.currentPage = newPage;  //  currentPage
      this.pageNum = newPage;      //  pageNum
    },

    handlePageSizeChange(newSize) {
      this.pageSize = newSize;     // 
      this.currentPage = 1;        // 
      this.pageNum = 1;            //  pageNum
    },
    startDrag(event) {
      this.isDragging = true; // 
      this.offsetX =
        event.clientX - this.$refs.draggableBox.getBoundingClientRect().left;
      this.offsetY =
        event.clientY - this.$refs.draggableBox.getBoundingClientRect().top;

      document.addEventListener("mousemove", this.doDrag);
      document.addEventListener("mouseup", this.stopDrag);
    },
    doDrag(event) {
      if (this.isDragging) {
        const left = event.clientX - this.offsetX;
        const top = event.clientY - this.offsetY;

        this.$refs.draggableBox.style.left = `${left}px`;
        this.$refs.draggableBox.style.top = `${top}px`;
        this.$refs.draggableBox.style.transform = "none"; //  `transform`
      }
    },
    stopDrag() {
      this.isDragging = false;
      document.removeEventListener("mousemove", this.doDrag);
      document.removeEventListener("mouseup", this.stopDrag);
    },
    closeWindow() {
      this.logDetail = false; //  false 
    },
    onSearch() {
      const form = this.searchForm;
      const params = {};
      if (form.photoName) {
        params.photo_name = form.photoName;
      }
      if (form.processType) {
        params.source = form.processType;
      }
      if (form.startTime) {
        params.start_time = form.startTime;
      }
      if (form.endTime) {
        params.end_time = form.endTime;
      }
      if (form.kindCount != null && form.kindCount !== '') {
        params.num_defects = form.kindCount;
      }

      console.log(form.photoName);
      console.log(form.processType);
      console.log(form.startTime);
      console.log(form.endTime);
      console.log(form.kindCount);
      console.log(params);

      this.$axios.post(this.$apiBaseUrl + "/query", params)
        .then(res => {
          const labelColors = {
            1: { color: 'rgb(0, 0, 255)', label: '' },
            2: { color: 'rgb(0, 255, 0)', label: '' },
            3: { color: 'rgb(255, 0, 0)', label: '' },
            4: { color: 'rgb(0, 255, 255)', label: '' }
          };

          this.tableData.photos = res.data.photos.map(item => {
            //  process_type
            if (item.process_type && typeof item.process_type === 'string') {
              item.process_type = JSON.parse(item.process_type.replace(/'/g, '"'));
            }

            // defect_info 
            if (item.defect_info && typeof item.defect_info === 'object') {
              item.num_defects = item.defect_info.num_defects || 0;

              const rawLabels = Array.isArray(item.defect_info.detected_labels)
                ? item.defect_info.detected_labels.filter(label => label !== 0)
                : [];

              item.detected_labels = rawLabels.join(', ');
              item.color_labels = rawLabels.map(label => labelColors[label]).filter(Boolean);
            } else {
              item.num_defects = 0;
              item.detected_labels = '';
              item.color_labels = [];
            }

            return item;
          });

          this.total = this.tableData.photos.length;
        })
        .catch(error => {
          console.error("", error);
        });
    },


    async handleButtonClick(photoName) {
      console.log(photoName);
      try {
        const response = await fetch(
          this.$apiBaseUrl + `/stream_video?name=${photoName}&style=2`
        );

        if (!response.ok) {
          throw new Error("Network response was not ok");
        }

        console.log(response.url);

        this.detailPhotoUrl = response.url;
        console.log(this.detailPhotoUrl);
        this.logDetail = true;
        console.log(this.logDetail);

      } catch (error) {
        console.error("There was a problem with the fetch operation:", error);
      }
    },
    handleSizeChange() { },
    handleCurrentChange(page) {
      this.currentPage = page;
    },
    saveVideo() {
      if (!this.videoName) {
        this.$message({
          type: "error",
          message: "",
        });
      } else {
        const videoUrl = `${this.$apiBaseUrl}/stream_video?name=${this.videoName}&style=2`;
        fetch(videoUrl)
          .then((response) => response.blob()) //  Blob
          .then((blob) => {
            //  URL
            const url = URL.createObjectURL(blob);

            //  <a> 
            const a = document.createElement("a");
            a.style.display = "none";
            a.href = url;
            a.download = "downloaded-video.mp4"; // 

            //  <a>  DOM 
            document.body.appendChild(a);
            a.click();

            //  URL  <a> 
            URL.revokeObjectURL(url);
            document.body.removeChild(a);
          })
          .catch((error) => {
            this.$message({
              type: "error",
              message: "",
            });
          });
      }
    },
    sendParameters() {
      let data = {
        size: this.size,
        detail: this.detail,
        fastElimination: this.fastElimination,
        dark: this.dark,
        CLAHE: this.CLAHE,
        Sharpening: this.Sharpening,
      };
      console.log(this.detail);
      console.log(this.fastElimination);


      return this.$axios
        .post(this.$apiBaseUrl + "/ConfirmParams", data)
        .then((res) => {

        });
    },
    // checkParameter(value) {
    //   var that = this;
    //   if (value) {
    //     // 
    //     const people_list = [that.people_attribute_enable];
    //     for (let i = 0; i < people_list.length; i++) {
    //       if (people_list[i]) {
    //         that.people_tracker_enable = true;
    //       }
    //     }
    //     const vehicle_list = [
    //       that.vehicle_attribute_enable,
    //       that.vehicle_license_enable,
    //       that.vehicle_press_detector_enable,
    //       that.vehicle_invasion_enable,
    //     ];
    //     for (let i = 0; i < vehicle_list.length; i++) {
    //       if (vehicle_list[i]) {
    //         that.vehicle_tracker_enable = true;
    //       }
    //     }
    //   } else {
    //     // 
    //     if (!(that.people_detector_enable && that.people_tracker_enable)) {
    //       that.people_attribute_enable = false;
    //     }
    //     if (!(that.vehicle_detector_enable && that.vehicle_tracker_enable)) {
    //       that.vehicle_attribute_enable = false;
    //       that.vehicle_license_enable = false;
    //       that.vehicle_press_detector_enable = false;
    //       that.vehicle_invasion_enable = false;
    //     }
    //   }
    // },
    resetVideo() {
      this.isShowVideo = false;
      this.isShowLocalVideo = true;
      this.videoName = "";
      this.videoUrl_ = "";
    },
    // dealWithVideo() {
    //   this.sendParameters()
    //     .then(() => {
    //       let data = {
    //         name: this.videoName,
    //       };
    //       console.log(data);
    //       return this.$axios.post(
    //         "http://localhost:8000/start_process_video",
    //         data
    //       );
    //     })
    //     .then(() => {
    //       this.getVideo();
    //     })
    //     .catch((error) => {
    //       console.error(":", error);
    //     });
    // },
    async dealWithVideo() {
      try {
        // 
        await this.sendParameters();

        let data = {
          name: this.videoName,
        };
        console.log(data);

        // 
        await this.$axios.post(
          this.$apiBaseUrl + "/start_process_video",
          data
        );
        console.log(this.progressPercentage);

        //  loading
        const loadingInstance = this.$loading({
          lock: true,
          text: `...${this.progressPercentage}%`,
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.7)",
        });

        // 
        await this.checkProgress(loadingInstance);

        // 100%
        await this.getVideo();
        await this.getLog();
        //  loading
        loadingInstance.close();
      } catch (error) {
        console.error(":", error);
        loadingInstance.close(); //  loading
      }
    },

    async getLog() {
      try {
        const response = await this.$axios.post(this.$apiBaseUrl + "/log");
        console.log(response);
        if (response.status === 200) {
          const convertedPeopleLog = response.data.people_log.map((item) => {
            return {
              id: "",
              time: item[0],
              location: `(${Math.floor(item[1][0])}, ${Math.floor(
                item[1][1]
              )}, ${Math.floor(item[1][2])}, ${Math.floor(item[1][3])})`,
              name: item[2],
            };
          });

          //  vehicle_log
          const convertedVehicleLog = response.data.vehicle_log.map((item) => {
            return {
              id: "",
              time: item[0],
              location: `(${Math.floor(item[1][0])}, ${Math.floor(
                item[1][1]
              )}, ${Math.floor(item[1][2])}, ${Math.floor(item[1][3])})`,
              name: item[2],
            };
          });

          // 
          const combinedLogs = [...convertedPeopleLog, ...convertedVehicleLog];

          //  paginatedData
          this.tableData = combinedLogs;
          this.total = this.tableData.length;
          console.log(this.tableData);
        }
      } catch (error) {
        console.error(
          ":",
          error.response ? error.response.data : error
        );
      }
    },
    // 
    async checkProgress(loadingInstance) {
      let progress = 0;
      while (progress < 100) {
        try {
          let data = {
            video_name: this.videoName,
          };
          const response = await this.$axios.post(
            this.$apiBaseUrl + "/get_progress",
            data
          );

          if (response.status === 200 && response.data.progress !== undefined) {
            progress = Math.round(response.data.progress); //  `progress` 
            console.log(`: ${progress}%`);
            this.progressPercentage = progress; // 
            loadingInstance.setText(`... ${this.progressPercentage}%`);
          } else {
            console.warn("Invalid response format");
          }
        } catch (error) {
          console.error("There was a problem with the fetch operation:", error);
        }
        // 
        await new Promise((resolve) => setTimeout(resolve, 1000));
      }
      this.progressPercentage = 0;
    },

    //  sleep 
    sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    },
    async getVideo() {
      try {
        const response = await fetch(
          this.$apiBaseUrl + `/stream_video?name=${this.videoName}&style=2`
        );

        if (!response.ok) {
          throw new Error("Network response was not ok");
        }

        console.log(response.url);

        this.videoUrl = response.url; //  URL  src
        console.log(this.videoUrl);
        this.isShowLocalVideo = false;
        this.isShowVideo = true; // 
      } catch (error) {
        console.error("There was a problem with the fetch operation:", error);
      }
    },
    toggleDrawer() {
      this.drawerVisible = !this.drawerVisible;
    },
    handleBeforeUpload(file) {
      const supportedFormats = ["mp4"];
      const fileExtension = file.name.split(".").pop().toLowerCase();
      const isSupportedFormat = supportedFormats.includes(fileExtension);

      const maxSize = 100 * 1024 * 1024; // 100 MB
      const isSizeValid = file.size <= maxSize;

      if (isSupportedFormat && isSizeValid) {
        this.showConfirmDialog(file);
        return false; // 
      } else {
        let errorMessage = "";
        if (!isSupportedFormat) {
          errorMessage += "mp4";
        }
        if (!isSizeValid) {
          errorMessage += " 100 MB";
        }
        this.$message.error(errorMessage);
        return false;
      }
    },
    showConfirmDialog(file) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          // 
          this.uploadFile(file);
        })
        .catch(() => {
          // 
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    handleError(error, file) {
      console.error(":", error);
      this.$message.error("");
    },
    handleSuccess(response, file) {
      this.$message({
        type: "success",
        message: "!",
      });
      const reader = new FileReader();
      reader.onload = (e) => {
        this.VideoUrl_ = e.target.result;
        this.isShowVideo = true;
      };
      reader.readAsDataURL(file);
      // 
    },
    handleProgress(event, file, fileList) {
      // this.progressPercentage = event.percent;
      // this.showProgress = true;
      // console.log(':', event.percent);
    },
    uploadFile(file) {
      const formData = new FormData();
      formData.append("video", file);

      const config = {
        headers: {
          "Content-Type": "multipart/form-data",
        },
        /*onUploadProgress: (progressEvent) => {
          const percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total);
          this.progressPercentage = percentCompleted;
          this.showProgress = true;
        }*/
      };
      // this.startProgressPolling();
      this.$axios
        .post(this.uploadUrl, formData, config)
        .then((response) => {
          this.handleSuccess(response, file);
          this.videoName = response.data.videoname;
        })
        .catch((error) => {
          this.handleError(error, file);
        });
      /*this.$axios.post(this.uploadUrl, formData, config).then(response => {
        this.videoName = response.data.videoname; //  { video_name: 'some-video-name' }
        console.log('Video name:', this.videoName);
        //this.startProgressPolling(); // 
        this.handleSuccess(response, file);
      }).catch(error => {
        this.handleError(error, file);
      });*/
    },
    startProgressPolling() {
      data = {
        name: this.videoName,
      };
      this.$axios
        .post(this.$apiBaseUrl + "/get_progress", data)
        .then((res) => {
          this.progress = res.progress;
        });
      const loading = this.$loading({
        lock: true,
        text: thisprogress,
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)",
      });
      setTimeout(() => {
        loading.close();
      }, 2000);
    },
  },
};
</script>

<style scoped>
.right-log {
  display: flex;
  flex-direction: column;
  width: 90%;
  height: 100%;
  background-color: white;
  margin-left: 2%;
  margin-right: 10%;
  border-radius: 8px;
}

.chaKanButton {
  width: 100%;
  font-size: small;
}

.right-log-head {
  height: 5vh;
  width: 100%;
  background-color: rgb(66, 159, 255);
  color: white;
  font-size: 1.5vw;
  line-height: 5vh;
  border-top-right-radius: 8px;
  /* :  */
  border-top-left-radius: 8px;
}

.bottom-ctrl {
  border-radius: 8px;
  /* :  */
  background-color: white;
  margin-top: 2vh;
  margin-bottom: 4vh;
  display: flex;
  /*  Flexbox */
  flex-direction: column;
  /*  */
  justify-content: space-evenly;
  /*  */
}

.bottom-ctrl-one {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.bottom-button {
  height: 8vh;
  width: 15vw;
  font-size: 1.5vw;
  margin-left: 0.5vw;
  margin-right: 0.5vw;
}

.upload {
  display: flex;
  flex-direction: column;
  /*  */
  justify-content: center;
  /*  */
  align-items: center;
  /*  */
  height: 100%;
  /*  */
  width: 100%;
  /*  */
  border: 1px dashed #dcdfe6;
  /* :  */
  border-radius: 8px;
  /* :  */
  margin-bottom: -2vh;
}

.upload div {
  width: 100%;
  height: 100%;
}

.upload .el-icon-upload {
  font-size: 20vw;
  /*  */
  margin-bottom: 3vh;
  /*  */
}

.hidden-column .cell {
  display: none;
}

.upload .el-upload__text {
  text-align: center;
  /*  */
  font-size: 3vw;
}

/deep/ .el-upload {
  width: 100%;
  height: 100%;
}

/deep/ .el-upload .el-upload-dragger {
  width: 100%;
  height: 100%;
}

.main {
  display: flex;
  height: 90%;
  width: 100%;
  justify-content: space-evenly;
}

.drawer-open {
  position: fixed;
  top: 10vh;
  right: 0;
  width: 15vw;
  height: 100%;
  background-color: #fff;
  box-shadow: -1px 0 2px rgba(0, 0, 0, 0.5);
  z-index: 1000;
  transform: translateX(0);
  transition: transform 0.3s ease;
  border-top-right-radius: 8px;
  /* :  */
  border-top-left-radius: 8px;
  user-select: none;
}

.drawer-close {
  position: fixed;
  top: 10vh;
  right: 0;
  width: 15vw;
  height: 100%;
  background-color: #fff;
  box-shadow: -1px 0 2px rgba(0, 0, 0, 0.5);
  z-index: 1000;
  transform: translateX(14vw);
  transition: transform 0.3s ease;
  border-top-right-radius: 8px;
  /* :  */
  border-top-left-radius: 8px;
  user-select: none;
}

.drawer-content {
  height: 100%;
  overflow-y: auto;
}

.drawer-content::-webkit-scrollbar {
  display: none;
  /*  Chrome, Safari, Edge  */
}

.drawer-button-bar {
  background-color: white;
  height: 100%;
  width: 1vw;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.drawer-button-open {
  position: absolute;
  top: 50%;
  left: 100%;
  /*  */
  transform: translate3d(-100%, -50%, 0);
  transition: all 0.5s;
}

.drawer-button-close {
  position: absolute;
  top: 50%;
  left: 100%;
  /*  */
  transform: translate3d(-100%, -50%, 0) rotateX(180deg);
  transition: all 0.5s;
}

/*video::-webkit-media-controls-play-button {
  display: none;
}
video::-webkit-media-controls-timeline {
  display: none;
}*/
</style>



