            console.log(jsonResponse); //  JSON 
            //  JSON  success  0
            if (jsonResponse.success === 0) {
              console.log("...");
              flag = 0; // 
            }
          } else if (contentType && contentType.includes("image/jpeg")) {
            // 
            this.photoUrl = url; //  URL  src
            console.log("URL:", this.photoUrl);
            flag = 1; //  1
            this.isShowPhoto = true; // 
            this.isShowLocalPhoto = false; // 
          } else {
            console.log("");
          }
        } catch (error) {
          console.error(":", error);
        }

        // 
        await new Promise(resolve => setTimeout(resolve, checkInterval));
      }
    },

    toggleDrawer() {
      this.drawerVisible = !this.drawerVisible;
    },
    handleBeforeUpload(file) {
      const supportedFormats = ["jpg", "jpeg", "png", "gif"];
      const fileExtension = file.name.split(".").pop().toLowerCase();
      const isSupportedFormat = supportedFormats.includes(fileExtension);

      const maxSize = 100 * 1024 * 1024; // 100 MB
      const isSizeValid = file.size <= maxSize;

      if (isSupportedFormat && isSizeValid) {
        this.showConfirmDialog(file);
        return false; // 
      } else {
        let errorMessage = "";
        if (!isSupportedFormat) {
          errorMessage += "";
        }
        if (!isSizeValid) {
          errorMessage += " 100 MB";
        }
        this.$message.error(errorMessage);
        return false;
      }
    },
    showConfirmDialog(file) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.uploadFile(file);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    uploadFile(file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        this.isShowPhoto = true;
        this.isShowLocalPhoto = true;
        this.photoUrl_ = e.target.result; //  data URL  photoUrl
      };
      reader.readAsDataURL(file);

      const formData = new FormData();
      formData.append("photo", file);

      this.$axios
        .post(this.uploadUrl, formData, {
          headers: {
            'Authorization': 'token5370ffbdcb08f3a7bfbf75287582539842d93c30', //  Authorization 
            'Content-Type': 'multipart/form-data' //  multipart/form-data
          }
        })
        .then((response) => {
          this.handleSuccess(response, file);
        })
        .catch((error) => {
          this.handleError(error, file);
        });
    },
    handleSuccess(response, file) {
      this.$message({
        type: "success",
        message: "!",
      });
      this.photoName = response.data.photoname;
    },
    handleError(error, file) {
      this.$message({
        type: "error",
      });
      console.error(":", error);
    },
    handleProgress(event, file) {
      console.log(":", event.percent);
    },
    onImageLoad() {
      console.log("");
    },
    onImageError() {
      console.error("");
    },
  },
};
</script>

<style scoped>
.right-log {
  display: flex;
  flex-direction: column;
  width: 90%;
  height: 100%;
  background-color: white;
  margin-left: 2%;
  margin-right: 10%;
  border-radius: 8px;
}

.right-log-head {
  height: 5vh;
  width: 100%;
  background-color: rgb(66, 159, 255);
  color: white;
  font-size: 1.5vw;
  line-height: 5vh;
  border-top-right-radius: 8px;
  /* :  */
  border-top-left-radius: 8px;
}

.bottom-ctrl {
  border-radius: 8px;
  /* :  */
  background-color: white;
  margin-top: 2vh;
  margin-bottom: 4vh;
  display: flex;
  /*  Flexbox */
  flex-direction: column;
  /*  */
  justify-content: space-evenly;
  /*  */
}

.bottom-ctrl-one {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.bottom-button {
  height: 8vh;
  width: 15vw;
  font-size: 1.5vw;
  margin-left: 0.5vw;
  margin-right: 0.5vw;
}

.upload {
  display: flex;
  flex-direction: column;
  /*  */
  justify-content: center;
  /*  */
  align-items: center;
  /*  */
  height: 100%;
  /*  */
  width: 100%;
  /*  */
  border: 1px dashed #dcdfe6;
  /* :  */
  border-radius: 8px;
  /* :  */
  margin-bottom: -2vh;
}

.upload div {
  width: 100%;
  height: 100%;
}

.upload .el-icon-upload {
  font-size: 20vw;
  /*  */
  margin-bottom: 3vh;
  /*  */
}

.upload .el-upload__text {
  text-align: center;
  /*  */
  font-size: 3vw;
}

/deep/ .el-upload {
  width: 100%;
  height: 100%;
}

/deep/ .el-upload .el-upload-dragger {
  width: 100%;
  height: 100%;
}

.main {
  display: flex;
  height: 90%;
  width: 100%;
  justify-content: space-evenly;
}

.drawer-open {
  position: fixed;
  top: 7vh;
  right: 0;
  width: 15vw;
  height: 100%;
  background-color: #fff;
  box-shadow: -1px 0 2px rgba(0, 0, 0, 0.5);
  z-index: 1000;
  transform: translateX(0);
  transition: transform 0.3s ease;
  user-select: none;
}

.drawer-close {
  position: fixed;
  top: 7vh;
  right: 0;
  width: 15vw;
  height: 100%;
  background-color: #fff;
  box-shadow: -1px 0 2px rgba(0, 0, 0, 0.5);
  z-index: 1000;
  transform: translateX(14vw);
  transition: transform 0.3s ease;
  user-select: none;
}

.drawer-content {
  height: 100%;
  overflow-y: auto;
}

.drawer-content::-webkit-scrollbar {
  display: none;
  /*  Chrome, Safari, Edge  */
}

.drawer-button-bar {
  background-color: white;
  height: 100%;
  width: 1vw;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.drawer-button-open {
  position: absolute;
  top: 50%;
  left: 100%;
  /*  */
  transform: translate3d(-100%, -50%, 0);
  transition: all 0.5s;
}

.drawer-button-close {
  position: absolute;
  top: 50%;
  left: 100%;
  /*  */
  transform: translate3d(-100%, -50%, 0) rotateX(180deg);
  transition: all 0.5s;
}

video::-webkit-media-controls-play-button {
  display: none;
}

video::-webkit-media-controls-timeline {
  display: none;
}
</style>



